import React, { useState } from "react";
import {
  Layout,
  Select,
  Button,
  Form,
  Input,
  Row,
  Col,
  Table,
  Tabs,
} from "antd";
import styles from "../css/Application.module.css";
import axios from "axios";
import { SearchOutlined } from "@ant-design/icons";
const { Option } = Select;
const { TabPane } = Tabs;
const { Header, Content } = Layout;

const columns = [
  { title: "", dataIndex: "", key: "" },
  { title: "", dataIndex: "", key: "" },
  { title: "/", dataIndex: "/", key: "/" },
  { title: "", dataIndex: "", key: "" },
  { title: "", dataIndex: "", key: "" },
  // { title: "", dataIndex: "applications", key: "applications" },
];

const Application = () => {
  //   
  const [courses, setCourses] = useState([]);
  const [selectMajor, setSelectMajor] = useState("");
  const [keyword, setKeyword] = useState("");
  const [selectedData, setSelectedData] = useState(null);
  const [addedData, setAddedData] = useState([]);

  //    
  const handleDataClick = (record) => {
    setSelectedData(record);
  };

  //     
  const handleAddButtonClick = () => {
    if (selectedData) {
      setAddedData((prevData) => [...prevData, selectedData]);
    }
  };

  const getCourses = async (major = "", keyword = "") => {
    try {
      const response = await axios.get(`/application/search`, {
        params: {
          major,
          keyword,
        },
      });
      setCourses(response.data);
    } catch (err) {
      console.error(err);
    }
  };

  const handleSelectChange = async (value) => {
    setSelectMajor(value);
    getCourses(value, keyword);
  };

  const handleKeywordChange = (e) => {
    setKeyword(e.target.value);
  };

  const handleEnter = () => {
    getCourses(selectMajor, keyword);
  };

  // 
  const onChange = (key) => console.log(`tab changed to ${key}`);

  return (
    <Layout>
      <Header className={styles.header}>
        <img src="logo.png" alt="logo" className={styles.logo} />
      </Header>

      <Layout>
        <Content className={styles.contentStyle}>
          <h1 className={styles.title}></h1>
          <div className={styles.formBackground}>
            <h3 className={styles.smallTitle}></h3>
            <Form layout="vertical">
              <Row gutter={16}>
                <Col span={4}>
                  <Form.Item label="">
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item label="">
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item label="">
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item label="/">
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item label="">
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item label="">
                    <Input />
                  </Form.Item>
                </Col>
              </Row>
            </Form>
          </div>

          {/*   */}
          <div className={styles.formBackground}>
            <h3 className={styles.smallTitle}> </h3>
            <Form layout="vertical">
              <Row gutter={16}>
                <Col span={4}>
                  <Form.Item>
                    <Select
                      defaultValue=""
                      onChange={handleSelectChange}
                      allowClear={true}
                    >
                      <Option value=""></Option>
                      <Option value=""></Option>
                      <Option value=""></Option>
                      <Option value=""></Option>
                    </Select>
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item>
                    <Input
                      placeholder=" "
                      suffix={
                        <SearchOutlined
                          className={styles.searchIcon}
                          onClick={handleEnter}
                        />
                      }
                      value={keyword}
                      onChange={handleKeywordChange}
                      onPressEnter={handleEnter}
                    />
                  </Form.Item>
                </Col>
              </Row>
            </Form>
          </div>

          {/*    */}
          <div className={styles.gutter16}>
            <div className={styles.flexWrapper}>
              <div className={styles.contentWrapper}>
                <h3 className={styles.smallTitle}></h3>
                <Table
                  dataSource={courses}
                  columns={columns}
                  onRow={(record) => ({
                    onClick: () => handleDataClick(record),
                  })}
                />
              </div>

              <div className={styles.button_wrapper}>
                <Button shape="circle" onClick={handleAddButtonClick}>
                  +
                </Button>
                <div className={styles.button_space}></div>
                <Button shape="circle">-</Button>
              </div>

              <div className={styles.contentWrapper}>
                <Tabs onChange={onChange} type="card">
                  <TabPane tab="" key="1">
                    {addedData.length > 0 ? (
                      <Table dataSource={addedData} columns={columns} />
                    ) : (
                      <p>  .</p>
                    )}
                  </TabPane>
                  <TabPane tab="" key="2">
                        2
                  </TabPane>
                </Tabs>
              </div>
            </div>
          </div>
        </Content>
      </Layout>
    </Layout>
  );
};

// eslint-disable-next-line no-lone-blocks
{
  /* <Col span={4}>
      <Form.Item>
        <Button type="primary" className={styles.queryButton} onClick={handleSearch}>
          
        </Button>
        <Button type="primary" className={styles.queryButton}>
          
        </Button>
      </Form.Item>
    </Col> */
}

export default Application;
