import React, { useState } from "react";
import { Form, Input, Select, Checkbox, Button, Typography, Row, Col, Modal } from "antd";
import { Link } from "react-router-dom";
import "../css/SignUp.css";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const { Option } = Select;
const { Title } = Typography;

function SignUp()
{
  const navigate = useNavigate();
  const [isModalVisible, setIsModalVisible] = useState(false) //     state

  const onFinish = (values) => {
    console.log(" ");
    console.log(values);
    const userInfo = {
      name: values.name,
      studentNum: values.studentNum,
      email: values.email,
      password: values.password,
      grade: values.grade,
      major: values.major
    };
    console.log(userInfo);

    axios
      .post("/signup", userInfo)
      .then((res) => {
        console.log(res.data);
        setIsModalVisible(true); //      .
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const handleOk = () => {
    setIsModalVisible(false); //      .
    navigate("/signin"); //       .
  };

  return (
    <div className="signup-container">
      <img src="/loginLogo.png" alt="loginLogo" className="loginLogo" />
      <Form
        name="signup"
        className="signup-form"
        initialValues={{
          remember: true,
        }}
        onFinish={onFinish}
      >
        <Form.Item
          name="name"
          rules={[
            {
              required: true,
              message: " ",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          name="studentNum"
          rules={[
            {
              required: true,
              message: " .",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          name="email"
          rules={[
            {
              required: true,
              message: "Please input your Email!",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          name="password"
          rules={[
            {
              required: true,
              message: " ",
            },
          ]}
        >
          <Input.Password placeholder="" />
        </Form.Item>

        <Form.Item
          name="confirm"
          dependencies={['password']}
          rules={[
            {
              required: true,
              message: "  ",
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('password') === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error('  .'));
              },
            }),
          ]}
        >
          <Input.Password placeholder=" " />
        </Form.Item>

      
        <Form.Item
          name="major"
          rules={[
            {
              required: true,
              message: " ",
            },
          ]}
        >
          <Select placeholder="">
            <Option value="consturction"></Option>
            <Option value="education"></Option>
            <Option value="statistic"></Option>
            <Option value="software"></Option>
            <Option value="data"></Option>
          </Select>
        </Form.Item>

        <Form.Item
          name="grade"
          rules={[
            {
              required: true,
              message: " !",
            },
          ]}
        >
          <Select placeholder="">
            <Option value="1">1</Option>
            <Option value="2">2</Option>
            <Option value="3">3</Option>
            <Option value="4">4</Option>
          </Select>
        </Form.Item>
        
        <Form.Item>
          <Button type="primary" htmlType="submit" className="signup-form-button">
            
          </Button>
        </Form.Item>
        
        <Form.Item>
         <p className="signin-text">   ? </p>
         <Link to="/signin" className="signin-link"></Link>
        </Form.Item>
      </Form>

      <Modal
        title=" "
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleOk}
      >
        <p> !</p>
      </Modal>
    </div>
  );
};



export default SignUp;